@import url(https://fonts.googleapis.com/css2?family=Overpass:wght@100;200;300&family=Yeseva+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.HomePage {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 3em;
    justify-content: center;
    padding: 0 10% 5% 10%;
}

.box {
    position: relative;
    color: transparent;
    width: 15em;
    height: 15em;
}

.box:hover {
    color: white;
    cursor: pointer;
}

.box img {
    position: absolute;
    width: 15em;
    height: 15em;
}

.innerbox {
    position: absolute;
    width: 15em;
    height: 15em;
    text-align: center;
    color: transparent;
}

.innerbox:hover {
    -webkit-backdrop-filter: blur(2px);
            backdrop-filter: blur(2px);
    color: white;
}

.innerbox h1 {
    font-family: 'Overpass', sans-serif;
    font-size: 1.3em;
    line-height: 2.2em;
    font-weight: 600;
    letter-spacing: 0.15em;
    text-transform: capitalize;
    padding-top: 20%;
}

.innerbox h2 {
    font-family: 'Overpass', sans-serif;
    font-size: 1em;
    line-height: 1.5em;
    font-weight: 400;
    letter-spacing: 0.15em;
    text-transform: uppercase;
    text-align: center;
}

@media screen and (max-width: 420px) {
    .HomePage {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-gap: 1em;
        justify-content: center;
        padding: 0 10% 5% 10%;
    }
    .box {
        position: relative;
        color: transparent;
        width: 8em;
        height: 8em;
    }
    .box img {
        position: absolute;
        width: 8em;
        height: 8em;
    }
    
    .innerbox {
        position: absolute;
        width: 8em;
        height: 8em;
        text-align: center;
        color: transparent;
    }
}
.Header{
    /* display: flex;
    align-items: center; */
    padding: 2% 10%;
}

.Header h1{
    text-align: center;
    font-family: 'Yeseva One', cursive;
    font-size: 2.5em;
    line-height: 2.7em;
    font-weight: 400;
    color: #ffd9d3;
    letter-spacing: 0.1em;
    text-transform: uppercase;
}

.Menu {
    display: flex;
    justify-items: center;
}

.Menu:hover {
    cursor: pointer;
}


@media screen and (max-width: 420px) {
    .Header h1{
        font-size: 1.8em;
        line-height: 2em;
    }
}

.Information {
    max-width: 1400px;
    padding: 2% 10%;
    display: flex;
    grid-gap: 8%;
    gap: 8%;
    align-items: flex-end;
    justify-content: center;
}

.Information img {
    width: 45%;
}

.Column {
    display: flex;
    flex-direction: column;
    grid-gap: 0.5em;
    gap: 0.5em;
    justify-content: left;
}

.Information h1 {
    font-family: 'Yeseva One', cursive;
    font-size: 2em;
    letter-spacing: 0.3em;
    text-transform: uppercase;
}

.Information h2 {
    font-family: 'Overpass', sans-serif;
    font-size: 1.5em;
    font-weight: 600;
    color: #95b47e;
}

.Information p {
    font-family: 'Overpass', sans-serif;
    font-size: 1em;
    font-weight: 400;
    letter-spacing: 0.025em;
    font-style: italic;
}

.Information p:nth-child(3) {
    font-style: normal;
    color: black;
}

.Information p:nth-child(4) {
    font-style: normal;
    color: black;
}

.Information a {
    text-decoration: none;
    color: #95b47e;
    font-family: 'Overpass', sans-serif;
    font-size: 1em;
    font-weight: 700;
    letter-spacing: 0.15em;
    background-color: #def2cf;
    padding: 0.5em 1em;
    border-radius: 0.2rem;
}

.Information a:hover {
    color: white;
}

.Pictures {
    display: flex;
    flex-direction: row;
    grid-gap: 1em;
    gap: 1em;
    margin-top: 2%;
    justify-content: space-between;
    padding: 2% 15%;
}

.Pictures img {
    width: 30%;
}

@media screen and (max-width: 420px) {
    .Information {
        padding: 10%;
        display: flex;
        flex-direction: column;
        grid-gap: 1em;
        gap: 1em;
        text-align: right;
    }
    
    .Information img {
        width: 300px;
    }

    .Pictures {
        padding: 2em;
        padding-bottom: 5em;
    }
}
.Footer {
    max-width: 1400px;
    padding: 0 10% 5% 10%;
}

.Footer h3 {
    font-family: 'Overpass', sans-serif;
    font-size: 1em;
    font-weight: 500;
    color: black;
    text-align: center;
}

.Footer a{
    color: #c9958c;
    text-decoration: none;
    font-weight: 700;
}

.Footer a:hover {
    color: #ffd9d3;
}

@media screen and (max-width: 420px) {
    .Footer h3 {
        font-size: 0.7em;
        font-weight: 500;
    }
}
.App {
  max-width: 1400px;
  justify-content: center;
  padding: 30px 80px;
  background-color: #fffff1;
}

